 .carousel {
    position: absolute;
    top: 15vh;
    right: 5vw;
    width: 35vw;
    height: 25vw;
    overflow: hidden;
    display: flex;
    flex-wrap: nowrap;
  }
  .carousel-img-item {
    width: 35vw;
    transition: all 2s;
  }
  .carousel-img-item img {
    width: 35vw;
    height: 25vw;
  }
  .carousel-point {
    position: absolute;
    bottom: 0;
    right: 0;
  }
  .carousel-point-item{
    display: inline-block;
    width: 0.8vw;
    height: 0.8vw;
    border: 2px solid black;
    border-radius: 50%;
  }
  @media screen and (max-width:1000px){
    .carousel {
      right: 22vw;
      width: 50vw;
      height: 40vw;
      overflow: hidden;
      display: flex;
      flex-wrap: nowrap;
    }
    .carousel-img-item {
      width: 50vw;
      height: 40vw;
      transition: all 2s;
    }
    .carousel-img-item img {
      width: 50vw;
      height: 40vw;
    }
  }
  